.LatestProducts_outer {
    width: 100%;
    /* background-color: white; */
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    /* position: absolute; */
    align-items: center;
    padding-top: 80px;
}

.LatestProducts_header {
    width: 100%;
    display: flex;
    justify-content: center;
    color: #151875;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 42px;
    line-height: 49.22px;
    /* position:absolute; */
    margin-top: 10px;
    padding: 10px;
}

.LatestProducts_header h4 {
    font-size: 2rem;
    /* font-weight: 650; */
    margin: 0;
}

.LatestProducts_menu {
    width: 50%;
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
    /* position: absolute; */


}

.LatestProducts_menu_col {
    /* width: 20%; */
    color: #151875;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    /* font-style: normal; */
    /* font-weight: 400; */
    /* font-size: .9rem; */
    font-weight: 500;
    /* line-height: 21.6px; */
    /* text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); */
}

.LatestProducts_menu_col:hover {
    color: #FB4997;
    border-bottom: 2px solid #FB4997;
}

.LatestProducts_outerimage_container {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    margin-top: 70px;
    margin-left: 30px;

}

.LatestProducts_inner_outerimage1_container1_row1 {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    margin-bottom: 60px;
}

.LatestProducts_inner_outerimage2_container2_row2 {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    margin-top: 30px;
    margin-top: 10px;
}


.LatestProducts_inner_inner_imagefull {
    width: 24%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    padding-bottom: 10px;

}

.LatestProducts_inner_top_image_row1 {
    background-color: #F7F7F7;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}

