.LatestProducts_imageicons1 {

    background-color: #EEEFFB;
    width: 35px;
    height: 35px;
    border-radius: 15px;
    margin-left: 15px;
    margin-bottom: 10px;
    margin-top: 5px;
    display: flex;
    justify-content: center;
    align-items: center;




}

.LatestProducts_imageicons1 img {
    width: 30px;

    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-left: 5px; */
    margin-bottom: 10px;
    margin-right: 10px;
    margin-top: 10px;





}

.LatestProducts_imageicons2 img {
    width: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 15px;
    margin-bottom: 15px;

}

.LatestProducts_bottom_imgchair_text_row2 {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    padding-bottom: 10px;
    margin-top: 10px;
}

.LatestProducts_bottom_imgchair_lefttext_row2 {
    width: 40%;
    display: flex;
    justify-content: left;
    color: #151875;
    font-family: 'Josefin Sans', sans-serif;
    font-size: .85rem;
    /* line-height: 18.75px; */
    border-bottom: 2px solid #EEEFFB;
    transform: matrix(1, 0.01, -0.01, 1, 0, 0);

}

.LatestProducts_bottom_imgchair_righttext_row2 {
    width: 50%;
    display: flex;
    justify-content: right;
}

.LatestProducts_bottom_imgchair_righttext_row2 label {
    padding-left: 10px;
    color: #151875;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 14px;
    line-height: 16.41px;
}

.LatestProducts_bottom_imgchair_righttext_row2 span {
    /* padding-left: 10px; */
    color: red;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 14px;
    line-height: 16.41px;
    text-decoration-line: line-through;
    padding-left: 10px;


}

@media only screen and (max-width: 500px) {
    .LatestProducts_inner_outerimage1_container1_row1,.LatestProducts_inner_outerimage2_container2_row2 {
        display: contents;
    }
    .LatestProducts_bottom_imgchair_text_row2{
        width: fit-content;
        gap: 17px;
        
    }

}